<template>
  <div class="mx-auto"  style="max-width: 960px">
   <app-detail-loading v-if="loading"></app-detail-loading>
    <div v-else>
      <v-row
              justify="center"
              no-gutters
      >
        <v-col
                cols="12" sm="12" xs="12" md="10" lg="10"
        >
          <v-breadcrumbs :items="items">
            <template v-slot:divider>
              <v-icon>mdi-chevron-right</v-icon>
            </template>
          </v-breadcrumbs>
          <app-carousel
                  :user="user"
                  :portfolio="portfolio"
          ></app-carousel>
        </v-col>
        <v-col
                cols="12" sm="12" xs="12" md="10" lg="10"
        >
          <app-content
                  :portfolio="portfolio"
                  :user="user"
                  :auth="auth"
                  :filterCategory="filterCate"
                  :rating="rating"
                  :reviews="reviews"
          ></app-content>
        </v-col>
        <v-col
                cols="12" sm="12" xs="12" md="10" lg="10"
                class="mt-2 hidden-md-and-up">
          <app-avatar :user="user" :auth="auth" :portfolio="portfolio"></app-avatar>
        </v-col>
        <v-col
                cols="10" class="mt-5"
        >
          <app-also-like
                  :categoryId="categoryId"
          ></app-also-like>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
  import appCarousel from '../../components/detail/Carousel'
  import appContent from '../../components/detail/Content'
  import appAlsoLike from '../../components/detail/AlsoLike'
  import appAvatar from '../../components/detail/Avatar'
  import appDetailLoading from '../../components/loading/DetailLoading'
  import { mapActions, mapGetters } from 'vuex'

  export default {
    components: {
      appCarousel,
      appContent,
      appAlsoLike,
      appAvatar,
      appDetailLoading
    },
    data: () => ({
      loading: true,
      categoryId: '',
      portfolio: {},
      user: {},
      reviews: [],
      rating: null,
      items: [
        {
          text: 'Home',
          to: { name: 'Home' },
          exact: true
        },
        {
          text: 'Detail',
          to: { name: 'Detail' },
        }
      ]
    }),
    computed: {
      ...mapGetters([
        'auth',
      ])
    },
    methods: {
      ...mapActions([
         'filterCategory'
      ]),

      async fetchReviews () {
        const result = await this.$axios.get('review/get-all', { params: { portfolioId: this.$route.params.id } })
        this.reviews = result.data.reviews
      },

      async fetchPortfolio () {
        const results = await this.$axios.get('home/detail', { params: { _id:  this.$route.params.id } })
        const portfolio = results.data.portfolio
        this.portfolio = portfolio
        this.user = portfolio.author
        this.categoryId = portfolio.categoryId._id
        // Rating
        const length = portfolio.reviews.length
        const arr = portfolio.reviews.map(i=>i.rating)
        let sum = arr.reduce((a, b) => a + b, 0)
        this.rating = sum/length
      },
      async filterCate (value) {
          this.$router.push({ name: 'Home', query: { by_category: value } })
      },
    },
    mounted () {
      this.$io.on('acceptWork', (id) => {
        if(this.user._id === id) {
          this.fetchPortfolio()
        }
      })
      this.$io.on('updatePortfolio', (id) => {
        if(this.$route.params.id === id) {
          this.fetchPortfolio()
        }
      })
      this.$io.on('deletePortfolio', (id) => {
        if(this.$route.params.id === id) {
          this.$router.push({ name: 'Home' })
        }
      })
    },
    async created () {
      await this.fetchPortfolio()
      await this.fetchReviews()
      setTimeout (() => {
        this.loading = false
      }, 200)
    }
  }
</script>
